*{
    margin:0;
    padding:0;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}
.row{
    display: flex;
    flex-wrap: wrap;
}
.item{
    width: 20%;
    height: 270px;
    color: #fff;
    cursor: pointer;
    align-items: center;
    justify-content: center;
    display: flex;
}